import React, { useState, useEffect } from 'react';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import { Request, Messages } from '../../../../shared';
import { useTranslation } from 'react-i18next';
import { pxToRem } from '../../../Shared/Components/SuperAgentThemeProvider/pxToRem';

export default function Welcome() {
    const { t } = useTranslation();

    const [version, setVersion] = useState('');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        async function getVersion() {
            try {
                const result = await new Request(Messages.Generic.GetVersion).sendRequest();
                const version = result.message.payload.version;   
                setVersion(version); 
            } catch(e) {
                setLoading(false);
            }
            setLoading(false);
        }
        
        try {
            getVersion();
        } catch(e) {
            setVersion("");
            setLoading(false);
        }
    }, []);

    if (loading) {
        return <CircularProgress />
    }

    return (
        <>
            <Typography variant="h1" sx={{
                fontSize: pxToRem(36),
                lineHeight: pxToRem(50),
                letterSpacing: pxToRem(0.72),
                fontWeight: 600,
            }}>
                {t('onboard.welcome.title', { version: version })}
            </Typography>
            <Typography variant="subtitle2">
                {t("onboard.welcome.description")}
            </Typography>
        </>
    );
}
