import React, { useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import { useTranslation } from 'react-i18next';
import Box from '@mui/material/Box';
import { menuList } from '../menuList';
import { NavLink, useLocation } from 'react-router-dom';

const totalSteps = menuList.length;

export default function NavigationButtons() {
    const location = useLocation();
    const [activeStep, setActiveStep] = useState(0);

    useEffect(() => {
        const currentIndex = menuList.findIndex((menuItem) => menuItem.route === location.pathname);
        setActiveStep(currentIndex);
    }, [location.pathname]);

    const { t } = useTranslation();

    if (activeStep < 0) {
        return null;
    }

    return (
        <Box sx={{
            marginLeft: {
                xs: '5px',
                sm: '48px',
            },
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'flex-end',
            flexGrow: 1,
            flexWrap: 'wrap',
            rowGap: '20px',
        }}>
            {activeStep > 0 ?
                <Button
                    component={NavLink} to={menuList[activeStep - 1].route}
                    color="secondary" variant="contained" sx={{maxWidth: '168px'}}>
                    {t("onboard.configuration.buttons.previous")}
                </Button> : <div />
            }
            {activeStep < totalSteps - 1 &&
                <Button
                    component={NavLink} to={menuList[activeStep + 1].route}
                    color="primary" variant="contained" sx={{maxWidth: '168px'}}>
                    {t("onboard.configuration.buttons.next")}
                </Button>
            }
            {activeStep === totalSteps - 1 &&
                <Button
                    component={NavLink} to="/done"
                    color="primary" variant="contained" sx={{maxWidth: '168px'}}>
                    {t("onboard.configuration.buttons.finish")}
                </Button>
            }
        </Box>
    );
}
