import { useMemo } from 'react';
import { createTheme, useMediaQuery } from '@mui/material';
import { themeOptions } from './theme';
import { darkPalette, lightPalette } from './palette';

const getPalette = (forcedTheme, prefersDarkMode) => {
    if (forcedTheme) {
        return forcedTheme === 'dark' ? darkPalette : lightPalette;
    }

    return prefersDarkMode ? darkPalette : lightPalette;
}

export const useAppTheme = (forcedTheme = null) => {
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
    const palette = getPalette(forcedTheme, prefersDarkMode);

    return useMemo(() => createTheme({ ...themeOptions, palette }), [palette]);
}
