//@ts-check
import BrowserHelper from "./BrowserHelper"

export class RatingHelper {

    /**
     * @param {string} userAgent
     */
    static storeURL(userAgent) {

        let browserName = BrowserHelper.browserName(userAgent);

        switch (browserName) {
            case "Safari":
            case "Mobile Safari":
                return "https://apps.apple.com/app/super-agent-for-safari/id1568262835"
            case "Chrome":
            case "Brave":
                return "https://chrome.google.com/webstore/detail/super-agent-beta/neooppigbkahgfdhbpbhcccgpimeaafi/"
            case "Firefox":
                return "https://addons.mozilla.org/en-US/firefox/addon/super-agent/";
            case "Edge":
                return "https://microsoftedge.microsoft.com/addons/detail/super-agent-automatic-c/mkdkilgodopmghhmfegikhieplbipfei";
            default:
                // On default open chrome 
                return "https://chrome.google.com/webstore/detail/super-agent-beta/neooppigbkahgfdhbpbhcccgpimeaafi/"
        }

    }

}