// Material UI
import { makeStyles } from '@mui/styles';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';
import CircularProgress from '@mui/material/CircularProgress';

// Owned
import { Request, Messages } from '../../../../../shared';

// External
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Preference from '../../../../Options/Components/Preferences/Views/Preference';

const useStyles = makeStyles((theme) => ({
  preferencesForm: {
    width: '100%',
    marginTop: '44px',
  }
}));

function PreferenceSelector(props) {
  const classes = useStyles();
  const { t } = useTranslation();

  const [loading, setLoading] = useState(true);
  const [preferences, setPreferences] = useState({
    advertising: false,
    functional: true,
    performance: true,
    other: false,
    notifications: false
  });

  const [preferencesSaved, setPreferencesSaved] = useState(false);

  const saveUserPreferences = async (userPreferences) => {
    await new Request(
      Messages.UserPreferences.SetUserPreferences, 
      { userPreferences: userPreferences }
    ).sendRequest();
  }

  useEffect(() => {
    new Request(Messages.UserPreferences.GetUserPreferences).sendRequest()
    .then(async (results) => {
      const userPreferences = results.message.payload.userPreferences;
      setLoading(false);
      setPreferences(userPreferences);
    })
  }, [props.isLoggedIn]);

  const handleChange = (event) => {
    setPreferences({ ...preferences, [event.target.name]: event.target.checked });
    saveUserPreferences({ ...preferences, [event.target.name]: event.target.checked });
    setPreferencesSaved( true );
  };

  return (
    loading ? <CircularProgress color="secondary"/> :
      <div>
        <FormControl component="fieldset" className={classes.preferencesForm}>
          <FormGroup sx={{
            display: 'flex',
            flexFlow: 'column wrap',
            flexDirection: 'row',
            justifyContent: 'space-between',
            rowGap: '48px',
            columnGap: '96px',
          }}>
            <Preference 
              title={t('options.preference_selector.advertising.title')}
              name="advertising"
              placeholder={t('options.preference_selector.advertising.placeholder')}
              description={t('options.preference_selector.advertising.description')}
              checked={preferences.advertising}
              handleChange={handleChange}
              small
            />
            <Preference 
              title={t('options.preference_selector.functional.title')}
              name="functional"
              placeholder={t('options.preference_selector.functional.placeholder')}
              description={t('options.preference_selector.functional.description')}
              checked={preferences.functional}
              handleChange={handleChange}
              small
            />
            <Preference 
              title={t('options.preference_selector.performance.title')}
              name="performance"
              placeholder={t('options.preference_selector.performance.placeholder')}
              description={t('options.preference_selector.performance.description')}
              checked={preferences.performance}
              handleChange={handleChange}
              small
            />
            <Preference 
              title={t('options.preference_selector.other.title')}
              name="other"
              placeholder={t('options.preference_selector.other.placeholder')}
              description={t('options.preference_selector.other.description')}
              checked={preferences.other}
              handleChange={handleChange}
              small
            />
          </FormGroup>
        </FormControl>
        <div>
          <Snackbar open={preferencesSaved} autoHideDuration={2000} onClose={() => { setPreferencesSaved(false); }}>
              <Alert severity="success">{t('general.saved_preferences')}</Alert>
          </Snackbar>
        </div>
      </div>
  )
}

export default PreferenceSelector;
