import React, { useState, useEffect } from "react";
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

// Owned
import LoginForm from "./LoginForm";
import { useStyles } from '../../styles';

import { Request, Messages } from '../../../../../shared';

import { useTranslation } from 'react-i18next';

export default function Login() {
    const classes = useStyles();
    const [loading, setLoading] = useState(true);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    
    const [credentials, setCredentials] = useState({
        username: '',
        password: ''
    });

    const [error, setError] = useState(null);
    const [errorMessage, setErrorMessage] = useState('');

    const { t } = useTranslation();

    const handleChange = (event) => {
        setCredentials(credentials => ({
            ...credentials,
            [event.target.id]: event.target.value
        }));
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        setLoading(true);

        new Request(Messages.Auth.SignInFromExtension, { credentials }).sendRequest()
        .then(() => {
            setLoading(false);
            setIsLoggedIn(true);
        })
        .catch((e) => {
            setLoading(false);
            setIsLoggedIn(false);
            switch(e.message.payload) {
                case 'background.wrong_credentials':
                    setErrorMessage(t("onboard.login.error_messages.incorrect_credentials"));
                    break;
                
                default:
                    setErrorMessage(t("onboard.login.error_messages.unknown_error"));
                    break;
            }
            setError(true);
        });
    }
    
    const initAuth = () => {
        new Request(Messages.Auth.CheckAuth, {}).sendRequest()
        .then(() => {
            setIsLoggedIn(true);
            setLoading(false);
        }).catch(() => {
            setIsLoggedIn(false);
            setLoading(false);
        })
    }

    useEffect(() => {
        const setupConnection = () => {
            var port = chrome.runtime.connect({ name: "optionsAuthListener" });
    
            port.onMessage.addListener(function (msg) {
            switch (msg.type) {
                case 'VALIDATE_AUTH':
                    initAuth();
                    break;
                default:
                    break;
            }
            });
        }

        initAuth();
        setupConnection();
    }, []);

    // Need a listener here to receive a message in case the user signs in so we can update state
    return(
        <Grid container spacing={0}>
            <Grid item xs={6}>
                <Typography variant="body1" sx={{ maxWidth: '420px' }}>
                    {t("onboard.login.description")}
                </Typography>
            </Grid>
            <Grid item xs={6} sx={{paddingLeft: '48px'}}>
                {loading ?
                    <CircularProgress color="secondary"/>
                    :
                    isLoggedIn ?
                        <div className={classes.paper}>
                            <Typography variant="body1">
                                {t("onboard.login.logged_in")}
                            </Typography>
                        </div>
                        :
                        <LoginForm handleChange={handleChange} handleSubmit={handleSubmit}/>
                }
                <Snackbar open={error} autoHideDuration={2000} onClose={() => { setError(false); }}>
                    <Alert severity="error">{errorMessage}</Alert>
                 </Snackbar>
            </Grid>
        </Grid>
    )
}
