import React, { useCallback, useState } from 'react';
import Drawer from '@mui/material/Drawer';
import Hidden from '@mui/material/Hidden';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';

import { makeStyles } from '@mui/styles';
import { useTheme } from '@mui/material/styles';
import { MobileDrawer } from './MobileDrawer';
import { openMainSite } from '../../Functions/openMainSite';
import { SuperAgentIcon } from '../../Icons/SuperAgentIcon/SuperAgentIcon';

const drawerWidth = 280;

const useStyles = makeStyles((theme) => ({
    appBar: {
        [theme.breakpoints.up('sm')]: {
            width: '100%',
            marginLeft: drawerWidth,
        },
    },
    menuButton: {
        marginRight: theme.spacing(2),
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    },
    logoButton: {
        [theme.breakpoints.down('xs')]: {
            paddingLeft: '0px'
        },
    },
    logoImage: {
        color: theme.palette.text.primary,
        height: 'auto',
        fontSize: '200px',
        maxHeight: '32px',
    },
    drawerPaper: {
        width: drawerWidth,
    },
}));

export const MobileHeader = ({ email, isLoggedIn, signOut, MenuComponent }) => {
    const theme = useTheme();
    const classes = useStyles();

    const [mobileOpen, setMobileOpen] = useState(false);

    const handleDrawerToggle = useCallback(() => {
        setMobileOpen(!mobileOpen);
    }, [mobileOpen]);

    const handleDrawerClose = useCallback(() => {
        setMobileOpen(false)
    }, []);

    return (
        <Hidden smUp implementation="css">
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={handleDrawerToggle}
                        className={classes.menuButton}
                    >
                        <MenuIcon/>
                    </IconButton>
                    <IconButton className={classes.logoButton}
                                edge="start"
                                color="inherit"
                                aria-label="menu"
                                onClick={openMainSite}>
                        <SuperAgentIcon className={classes.logoImage} />
                    </IconButton>
                </Toolbar>
            </AppBar>

            <nav
                aria-label="Menu Items"
                sx={{
                    display: {
                        xs: 'none'
                    }
                }}
            >
                <Drawer
                    variant="temporary"
                    anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                    open={mobileOpen}
                    onClose={handleDrawerToggle}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                >
                    <MobileDrawer
                        handleDrawerClose={handleDrawerClose}
                        email={email}
                        isLoggedIn={isLoggedIn}
                        signOut={signOut}
                        MenuComponent={MenuComponent}
                    />
                </Drawer>
            </nav>
        </Hidden>
    );
};

