import React, { useCallback } from 'react';
import Typography from '@mui/material/Typography';
import { useTranslation } from 'react-i18next';
import { RatingHelper } from '../../../shared/RatingHelper';
import { makeStyles } from '@mui/styles';
import { StarIcon } from '../../Shared/Icons/StarIcon/StarIcon';
import { pxToRem } from '../../Shared/Components/SuperAgentThemeProvider/pxToRem';

const useStyles = makeStyles(theme => ({
    background: {
        display: 'flex',
        alignItems: 'center',
        background: theme.palette.primary.main,
        padding: 20,
        borderRadius: '0px 0px 30px 30px',
        justifyContent: 'space-around',
        width: '100%',
        maxWidth: '496px',
        height: 48,
        alignSelf: 'center',
    },
    stars: {
        display: 'flex',
        cursor: 'pointer',
        fontSize: pxToRem(10),
    },
    starIcon: {
        fontSize: 'inherit',
    }
}));

export const RateButton = () => {
    const classes = useStyles();
    const { t } = useTranslation();

    const onRatingClick = useCallback(() => {
        const storeURL = RatingHelper.storeURL(navigator.userAgent);
        window.open(storeURL);
    }, []);

    return (
        <div className={classes.background}>
            <Typography variant="body1">
                {t('options.dashboard.rate_me.title')} {t('options.dashboard.rate_me.button')}
            </Typography>
            <Typography className={classes.stars} variant="body1" onClick={onRatingClick}>
                <StarIcon className={classes.starIcon} />
                <StarIcon className={classes.starIcon} />
                <StarIcon className={classes.starIcon} />
                <StarIcon className={classes.starIcon} />
                <StarIcon className={classes.starIcon} />
            </Typography>
        </div>
    );
}
