import React, { useCallback } from 'react';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { useTranslation } from 'react-i18next';
import Box from '@mui/material/Box';

export default function Analytics() {
    const { t } = useTranslation();
    
    const openPrivacyPolicy = useCallback(() => {
        chrome.tabs.create({url: "https://www.super-agent.com/privacy-policy"}, (tab) => {
            
        });
    }, []);

    return(
        <Grid container spacing={0} sx={{ maxWidth: '528px' }}>
            <Grid item xs={12}>
                <Typography variant="body1">
                    {t("onboard.analytics.description.first_paragraph")}
                </Typography>
                <Typography variant="body1" sx={{marginTop: '30px', marginBottom: '48px'}}>
                    {t("onboard.analytics.description.second_paragraph")}    
                </Typography>
                <br />
                <Typography variant="body1">
                    {t("onboard.analytics.description.read_more")}
                    <Box sx={{ textDecoration: 'underline', cursor: 'pointer', display: 'inline' }} onClick={openPrivacyPolicy}>
                        {t("onboard.analytics.description.privacy_policy")}
                    </Box>
                </Typography>
            </Grid>
        </Grid>
    )
}
