import React from 'react';
import ScopedCssBaseline from '@mui/material/ScopedCssBaseline';

export const MobileDrawer = ({ handleDrawerClose, isLoggedIn, email, signOut, MenuComponent }) => {
    return (
        <ScopedCssBaseline enableColorScheme>
            <div onClick={handleDrawerClose} onKeyDown={handleDrawerClose}>
                <MenuComponent isLoggedIn={isLoggedIn} email={email} signOut={signOut} disableLogo/>
            </div>
        </ScopedCssBaseline>
    );
}
