import React from "react";
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';

import Popup from '../../../../Shared/Img/Popup.png';
import SuccessLogo from '../../../../Shared/Img/success-dark.png';
import Logo from '../../../../Shared/Img/warning-lite.png';

import { useTranslation } from 'react-i18next';
import Box from '@mui/material/Box';

const switchInfoStyle = {
    display: 'flex',
    flexDirection: 'row',
    columnGap: '14px',
    alignItems: 'center',
};

export default function Info() {
    const { t } = useTranslation();

    return(
        <Grid container spacing={0}>
            <Grid item xs={6}>
                <Typography variant="body1" sx={{ maxWidth: '420px' }}>
                    {t("onboard.info.description")}
                </Typography>
                <Box style={{ marginTop: '44px', display: 'flex', flexDirection: 'column', rowGap: '24px' }}>
                    <Box style={switchInfoStyle}>
                        <img src={Logo} width="32px" alt="No Action"/>
                        <Typography variant="subtitle2">
                            {t("onboard.info.actions.no_action")}
                        </Typography>
                    </Box>
                    <Box style={switchInfoStyle}>
                        <img src={SuccessLogo} width="32px" alt="Success"/>
                        <Typography variant="subtitle2">
                            {t("onboard.info.actions.acted")}
                        </Typography>
                    </Box>
                </Box>
            </Grid>
            <Grid item xs={6} style={{textAlign: "right"}}>
                <img src={Popup} alt="Popup" style={{width: '70%'}}/>
            </Grid>
        </Grid>
    )
}
