import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Messages, Request } from '../../../../shared';
import { makeStyles } from '@mui/styles';
import Divider from '@mui/material/Divider';
import { MenuList } from './MenuList';
import { LogOutIcon } from '../../Icons/LogOutIcon/LogOutIcon';
import { LogInIcon } from '../../Icons/LogInIcon/LogInIcon';

const useStyles = makeStyles(() => ({
    menu: {
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        alignItems: 'flex-start',
    },
    email: {
        wordBreak: 'break-all',
    },
    footer: {
        display: 'flex',
        flexDirection: 'column',
        gap: '50px',
        marginBottom: 50,
        width: '100%',
    },
    logout: {
        textTransform: 'none',
        color: 'inherit',
        justifyContent: 'start',
        columnGap: '22px',
    },
    logoutIcon: {
        fontSize: '21px!important',
    }
}));

const Menu = ({ isLoggedIn, email, signOut, displayFooter, menuList, disableLogo }) => {
    const { t } = useTranslation();
    const classes = useStyles();

    const openSignIn = useCallback(() => {
        new Request(Messages.Auth.OpenSignIn, {}).sendRequest();
    }, []);

    return (
        <div className={classes.menu}>
            <MenuList menuList={menuList} disableLogo={disableLogo} />

            { displayFooter &&
                <div className={classes.footer}>
                    <Divider sx={{margin: 0}}/>
                    <Typography variant="subtitle2" className={classes.email}>
                        {isLoggedIn ? email : t('options.appbar.localstorage')}
                    </Typography>
                    <Button
                        variant="text"
                        color="primary"
                        className={classes.logout}
                        startIcon={isLoggedIn ? <LogOutIcon className={classes.logoutIcon}/> : <LogInIcon/>}
                        onClick={isLoggedIn ? signOut : openSignIn}
                    >
                        <Typography variant="subtitle1">
                            {isLoggedIn ? t('options.menu.logout') : t('options.menu.signin')}
                        </Typography>
                    </Button>
                </div>
            }
        </div>
    );
};

export default Menu;
