import Menu from '../../Shared/Components/ResponsiveDrawer/Menu';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { menuList as menuListGenerator } from './menuList';

export const OnboardMenu = (props) => {
    const { t } = useTranslation();
    const [menuList, setMenuList] = useState([]);

    useEffect(() => {
        const createdMenuList = menuListGenerator.map(menuItem => {
            return {
                ...menuItem,
                text: menuItem.text(t),
            }
        });
        setMenuList(createdMenuList)
    }, [t]);

    return <Menu
        {...props}
        menuList={menuList}
    />
}
