// React
import React, { useCallback, useMemo, useState } from 'react';

// Material UI
import Switch from '@mui/material/Switch';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';

// External
import { useTranslation } from 'react-i18next';
import { pxToRem } from '../../../../Shared/Components/SuperAgentThemeProvider/pxToRem';
import Box from '@mui/material/Box';
import { useAppTheme } from '../../../../Shared/Components/SuperAgentThemeProvider/useAppTheme';
import { ThemeProvider } from '@mui/material/styles';
import ScopedCssBaseline from '@mui/material/ScopedCssBaseline';

const cardStyle = {
    paper: {
        minHeight: '336px',
    },
    title: {
        width: {
            xs: '100%',
            sm: '66%',
        },
        height: '112px',
        lineHeight: pxToRem(50),
    },
}

const flatStyle = {
    paper: {
        padding: '0px',
        display: 'flex',
        justifyContent: 'start',
        borderRadius: '0',
        backgroundColor: 'inherit',
        border: 'none',
    },
    title: {
        marginBottom: '6px',
    },
    showMoreLess: {
        marginTop: '12px',
        marginBottom: '30px',
    },
    switch: {
        width: '100%',
    }
}

const smallStyle = {
    paper: {
        borderRadius: '60px',
        borderColor: (theme) => theme.palette.background.default,
        backgroundColor: (theme) => theme.palette.secondary.main,
        minHeight: '224px',
        padding: '32px',
    },
    title: {
        fontSize: pxToRem(24),
        letterSpacing: pxToRem(0.24),
        lineHeight: pxToRem(50),
        fontWeight: 600,
        height: 'auto',
    },
}

const paperStyle = ({ variant, small }) => ({
    margin: '0px',
    padding: '36px 48px 50px',
    position: 'relative',
    maxWidth: {
        xs: '95vw',
        sm: '528px',
    },
    display: 'grid',
    flexDirection: 'column',
    justifyContent: 'space-between',
    textAlign: 'left',
    borderRadius: '60px',
    backgroundColor: (theme) => theme.palette.background.default,
    boxShadow: 'none',
    border: (theme) => `2px solid ${theme.palette.text.primary}`,
    width: '100%',
    ...(variant === 'card' && cardStyle.paper),
    ...(variant === 'flat' && flatStyle.paper),
    ...(small && smallStyle.paper),
});

const titleStyle = ({ variant, small }) => ({
    ...(variant === 'card' && cardStyle.title),
    ...(variant === 'flat' && flatStyle.title),
    ...(small && smallStyle.title),
});

const detailsStyle = {
    lineHeight: 1,
    letterSpacing: '0.07px',
};

const showMoreLessStyle = ({ variant }) => ({
    cursor: 'pointer',
    marginTop: '16px',
    marginBottom: '31px',
    textDecoration: 'underline',
    textTransform: 'lowercase',
    lineHeight: 1,
    ...(variant === 'flat' && flatStyle.showMoreLess),
})

const switchStyle = ({ variant }) => ({
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignSelf: 'end',
    alignItems: 'end',
    ...(variant === 'flat' && flatStyle.switch),
});

const checkedStyle = ({ checked }) => ({
    display: 'inline',
    ...(checked && { color: (theme) => theme.palette.primary.main }),
});

function Preference(props) {
    const variant = props.variant || 'card';
    const lightTheme = useAppTheme(variant !== 'flat' ? 'light' : null);
    const small = props.small || false;
    const [showMore, setShowMore] = useState(false);

    const toggleShowMore = useCallback(() => {
        setShowMore(!showMore);
    }, [showMore]);

    const { t } = useTranslation();
    const showMoreLess = useMemo(() => t(showMore ? 'general.show_less' : 'general.show_more'), [t, showMore]);

    const paperSx = useMemo(() => paperStyle({ variant, small }), [variant, small]);
    const titleSx = useMemo(() => titleStyle({ variant, small }), [variant, small]);
    const showMoreLessSx = useMemo(() => showMoreLessStyle({ variant }), [variant]);
    const switchSx = useMemo(() => switchStyle({ variant }), [variant]);
    const checkedSx = useMemo(() => checkedStyle({ checked: props.checked }), [props.checked]);

    return (
        <ThemeProvider theme={lightTheme}>
            <Paper elevation={0} sx={paperSx}
                   enableColorScheme
                   component={ScopedCssBaseline}>
                <Typography variant={variant === 'flat' ? 'h4' : 'h1'} sx={titleSx}>
                    {props.title}
                </Typography>
                <Typography variant={variant === 'flat' ? 'body1' : 'subtitle2'} sx={detailsStyle}>
                    { showMore? props.description : props.placeholder }
                </Typography>
                <Typography variant="body1" onClick={toggleShowMore} sx={showMoreLessSx}>
                    { showMoreLess }
                </Typography>
                <Box sx={switchSx}>
                    <Typography variant="subtitle2">
                        {t('general.your_choice')}&nbsp;
                        <Typography sx={checkedSx} variant="subtitle1">
                            {props.checked ? t('general.accept') : t('general.reject')}
                        </Typography>
                    </Typography>
                    <Switch checked={props.checked} onChange={props.handleChange} name={props.name}/>
                </Box>
            </Paper>
        </ThemeProvider>
    )
}

export default Preference;
