import { switchClasses } from '@mui/material';
import { pxToRem } from './pxToRem';

const borderWidth = 2;
const widthValue = 38;
const heightValue = 24;
const sizeValue = 14;

const width = pxToRem(widthValue);
const height = pxToRem(heightValue);
const size = pxToRem(sizeValue);
const gap = (heightValue - sizeValue) / 2;

const switchTheme = {
    root: ({ theme }) => ({
        width,
        height,
        padding: 0,
        margin: theme.spacing(0),
        overflow: 'unset',
    }),
    switchBase: ({ theme }) => ({
        padding: pxToRem(gap),
        [`&.${switchClasses.checked}`]: {
            transform: `translateX(calc(${width} - ${size} - ${pxToRem(2 * gap)}))`,
        },
        [`&.${switchClasses.checked} + .${switchClasses.track}`]: {
            backgroundColor: theme.palette.primary.main,
            opacity: 1,
        },
        [`&.${switchClasses.checked} .${switchClasses.thumb}`]: {
            backgroundColor: theme.palette.text.darkPrimary,
        },
    }),
    track: ({ theme }) => ({
        borderRadius: 40,
        border: `solid ${theme.palette.text.primary}`,
        borderWidth,
        backgroundColor: theme.palette.mode === 'dark' ? theme.palette.background.default : theme.palette.secondary.main,
        opacity: 1,
        transition: theme.transitions.create(['background-color']),
        boxSizing: 'border-box',
    }),
    thumb: ({ theme }) => ({
        boxShadow: 'none',
        backgroundColor: theme.palette.text.primary,
        width: size,
        height: size,
    }),
    checked: {},
};

export default switchTheme;
