import React from "react";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";

import { useTranslation } from 'react-i18next';
import { pxToRem } from '../../../Shared/Components/SuperAgentThemeProvider/pxToRem';
import Box from '@mui/material/Box';

export default function Done() {
    const { t } = useTranslation();

    return(
        <Box sx={{maxWidth: '528px'}}>
            <Typography variant="h1" sx={{
                fontSize: pxToRem(36),
                lineHeight: pxToRem(50),
                letterSpacing: pxToRem(0.72),
                fontWeight: 600,
            }}>
                {t("onboard.done.title")} 
            </Typography>
            <Typography variant="body1">
                {t("onboard.done.description")}
            </Typography>
            <Box sx={{ marginTop: '48px', display: 'flex', justifyContent: 'space-between' }}>
                <Button
                    sx={{ maxWidth: '168px' }}
                    color="secondary" variant="contained" onClick={() => { chrome.runtime.openOptionsPage(); }}>
                    {t("onboard.done.buttons.open_settings")}
                </Button>
                <Button
                    sx={{ maxWidth: '168px' }}
                    color="primary" variant="contained" onClick={() => { window.close(); }}>
                    {t("onboard.done.buttons.close_window")}
                </Button>
            </Box>
        </Box>
    )
}
