import React, { useState } from 'react';
import Grid from '@mui/material/Grid';
import { makeStyles } from '@mui/styles';

import NoInternetConnection from '../../View/NoInternetConnection';
import { RateButton } from '../../../Options/Components/RateButton';
import { ThemeProvider } from '@mui/material/styles';
import { useAppTheme } from '../SuperAgentThemeProvider/useAppTheme';
import ScopedCssBaseline from '@mui/material/ScopedCssBaseline';

const useStyles = makeStyles(theme => ({
    menuGrid: {
        padding: '0 2px',
        [theme.breakpoints.up('sm')]: {
            flexBasis: '20%',
            maxWidth: '20%',
        },
        [theme.breakpoints.up('lg')]: {
            padding: '0 48px',
        },
    },
    mainGrid: {
        display: 'flex',
        flexDirection: 'column',
        padding: theme.spacing(1),
        paddingTop: 0,
        paddingBottom: 0,
        [theme.breakpoints.up('sm')]: {
            padding: theme.spacing(2),
            paddingTop: 0,
            paddingBottom: 0,
            flexBasis: '80%',
            maxWidth: '80%',
        },
    },
    contentWrapper: {
        marginTop: '48px',
        display: 'flex',
        flexDirection: 'column',
        maxWidth: '1152px',
        width: '100%',
        height: '100%',
        alignSelf: 'center',
        [theme.breakpoints.down('sm')]: {
            marginBottom: '10px',
        }
    }
}));

export const RouteContainer = ({ props }) => {
    const classes = useStyles();
    const [isOnline] = useState(navigator.onLine);

    const lightTheme = useAppTheme('light');
    const appTheme = useAppTheme(props.forcedTheme);

    if (!isOnline) {
        return <NoInternetConnection/>;
    }

    const { ContentComponent, MenuComponent } = props;

    return (
        <Grid container spacing={0}>
            <ThemeProvider theme={lightTheme}>
                <Grid
                    item
                    xs={0}
                    sm={3}
                    className={classes.menuGrid}
                    sx={{
                        display: {
                            xs: 'none',
                            sm: 'block'
                        },
                    }}
                    enableColorScheme
                    component={ScopedCssBaseline}
                >
                    <MenuComponent isLoggedIn={props.isLoggedIn} email={props.email} signOut={props.signOut}/>
                </Grid>
            </ThemeProvider>
            <ThemeProvider theme={appTheme}>
                <Grid item xs={12} sm={9} className={classes.mainGrid}
                      sx={{
                          backgroundColor: (theme) => theme.palette.secondary.main,
                      }}
                      enableColorScheme
                      component={ScopedCssBaseline}>
                    { props.showRateButton && <RateButton/> }
                    <div className={classes.contentWrapper}>
                        <ContentComponent {...props} />
                    </div>
                </Grid>
            </ThemeProvider>
        </Grid>
    );
}
