import React from 'react';
import List from '@mui/material/List';
import { MenuListItem } from './MenuListItem';
import { SuperAgentIcon } from '../../Icons/SuperAgentIcon/SuperAgentIcon';
import { makeStyles } from '@mui/styles';
import { pxToRem } from '../SuperAgentThemeProvider/pxToRem';

const useStyles = makeStyles(() => ({
    mainIcon: {
        fontSize: pxToRem(280),
        height: 'auto',
        maxWidth: '100%',
        marginTop: 48,
        marginBottom: 58,
    }
}));

export const MenuList = ({ menuList, disableLogo }) => {
    const classes = useStyles();

    return (
        <List component="nav" aria-label="main mailbox folders" sx={{width: '100%'}}>
            { !disableLogo && <SuperAgentIcon className={classes.mainIcon}/> }
            {
                menuList.map(({route, icon, text}) => (
                    <MenuListItem key={route} route={route} icon={icon} text={text} />
                ))
            }
        </List>
    );
};
