import React from 'react';
import Box from '@mui/material/Box';
import Welcome from './Welcome/Welcome';
import NavigationButtons from './Configuration/NavigationButtons';
import Divider from '@mui/material/Divider';
import { Redirect, Route, Switch } from 'react-router-dom';
import Login from './Configuration/Login/Login';
import Info from './Configuration/Info/Info';
import Preferences from './Configuration/Preferences/Preferences';
import Analytics from './Configuration/Analytics/Analytics';
import Done from './Done/Done';

export const OnboardContent = () => {
    return (
        <>
            <Box sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    flexWrap: 'wrap',
                    marginTop: {
                        xs: '0px',
                        sm: '86px',
                    },
                    rowGap: '20px'
                }}>
                <Box sx={{ display: 'flex', flexDirection: 'column', flexBasis: '50%' }}>
                    <Welcome/>
                </Box>
                <NavigationButtons />
            </Box>
            <Divider sx={{ margin: '48px 0px' }}/>
            <Switch>
                <Redirect exact from="/" to="/how-to"/>
                <Route exact path="/how-to">
                    <Info/>
                </Route>
                <Route path="/user-account">
                    <Login/>
                </Route>
                <Route path="/preferences">
                    <Preferences/>
                </Route>
                <Route path="/data-collection">
                    <Analytics/>
                </Route>
                <Route path="/done">
                    <Done/>
                </Route>
            </Switch>
        </>
    )
}
