import { makeStyles } from '@mui/styles';
import FontConstants from '../../Shared/fontConstants';

export const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(3),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  welcomeHeader: {
    marginTop: theme.spacing(3),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  paperNoMargin: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  imgHeader: {
    margin: "20px"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  testExtensionContent: {
    marginTop: "40px",
    marginLeft: "10px",
    marginRight: "10px"
  },
  testExtensionTitle: {
    fontWeight: 900,
    fontSize: 28,
    marginTop: "15px",
    color: "white",
    whiteSpace: "pre-line"
  },
  testExtensionSubtitle: {
    fontWeight: FontConstants.medium,
    marginTop: "5px",
    color: "lightGrey",
    whiteSpace: "pre-line"
  },
  testExtensionFooter: {
    marginTop: "50px !important",
    fontSize: "12px !important",
    color: "lightGrey"
  },
  findExtensionSettingsToolbar: {
    paddingTop: '10px', 
    paddingBottom: '10px', 
    marginTop: '20px', 
    borderRadius: '25px', 
    width: 'auto', 
    left: '0', 
    maxWidth: "100%", 
    backgroundColor: theme.palette.secondary.main, 
    color: theme.palette.primary.main
  },
  findExtensionSettingsToolbarText: {
    marginLeft: '10px'
  },
  congratulationsConfettiContainer: {
    width: "1px", 
    margin: "0 auto"
  },
  testExtensionFooterMail: {
    textDecoration: 'none', 
    color: 'white'
  },
  horizontalLogo: {
    paddingLeft:"20px", 
    paddingRight: "20px"
  },
  configurationStepper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(5)
  },
  gridIcon: {
    textAlign: "center"
  },
  stepperNavigation: {
    marginTop: theme.spacing(2),
    textAlign: "right"
  },
  infoIconDescription: {
    "textAlign": "left"
  },
  loginInput: {
    "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
      borderColor: "#FDC737"
    },
    "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
      borderColor: "#FDC737"
    },
    "& .MuiInputLabel-outlined": {
      color: "#FDC737"
    },
    "& .MuiOutlinedInput-input": {
      color: "#FDC737"
    }
  }
}));
