import switchTheme from './switch';
import { pxToRem } from './pxToRem';
import { radioClasses } from '@mui/material';
import FontConstants from '../../fontConstants';

export const themeOptions = {
    breakpoints: {
        values: {
            xs: 0,
            sm: 700,
            md: 900,
            lg: 1200,
            xl: 1536,
        },
    },
    typography: {
        fontFamily: [
            'Instrument Sans'
        ],
        body1: {
            fontSize: pxToRem(14),
            letterSpacing: pxToRem(0.14),
            lineHeight: pxToRem(18),
            fontWeight: FontConstants.medium,
        },
        body2: {
            fontSize: pxToRem(14),
            letterSpacing: pxToRem(0.14),
            fontWeight: FontConstants.normal,
        },
        h1: {
            fontSize: pxToRem(47),
            letterSpacing: pxToRem(0.47),
            fontWeight: 600,
        },
        h4: {
            fontSize: pxToRem(14),
            letterSpacing: pxToRem(0.42),
            lineHeight: pxToRem(18),
            fontWeight: 600,
            textTransform: 'uppercase',
        },
        subtitle1: {
            fontSize: pxToRem(17),
            letterSpacing: pxToRem(0.17),
            lineHeight: pxToRem(20),
            fontWeight: FontConstants.medium,
        },
        subtitle2: {
            fontSize: pxToRem(17),
            letterSpacing: pxToRem(0.17),
            lineHeight: pxToRem(20),
            fontWeight: FontConstants.normal,
        }
    },
    components: {
        MuiDivider: {
            styleOverrides: {
                root: ({ theme }) => ({
                    borderColor: theme.palette.mode === 'dark' ? theme.palette.secondary.light : theme.palette.primary.main,
                    borderWidth: 1,
                    marginTop: 14,
                    marginBottom: 14,
                }),
            },
        },
        MuiSwitch: {
            styleOverrides: switchTheme,
        },
        MuiButton: {
            styleOverrides: {
                contained: ({ theme }) => ({
                    height: 48,
                    border: `2px solid ${theme.palette.text.primary}`,
                    borderRadius: 10,
                    boxShadow: 'none',
                    fontSize: pxToRem(14),
                    letterSpacing: pxToRem(0.42),
                    color: theme.palette.text.primary,
                    textTransform: 'capitalize',
                    fontWeight: 600,
                    width: '100%',
                }),
            },
        },
        MuiRadio: {
            styleOverrides: {
                root: ({ theme }) => ({
                    color: theme.palette.text.primary,
                    [`&.${radioClasses.checked}`]: {
                        color: theme.palette.text.primary,
                    },
                    fontWeight: FontConstants.medium,
                }),
            }
        },
        MuiTextField: {
            styleOverrides: {
                root: ({ theme }) => ({
                    '.MuiInputBase-root': {
                        backgroundColor: theme.palette.background.default,
                        borderRadius: '10px',
                    },
                    '.MuiFormLabel-root': {
                        color: `${theme.palette.text.primary}!important`,
                        opacity: 0.5,
                        fontWeight: FontConstants.medium,
                        transform: 'translate(24px, 16px) scale(1)',
                    },
                    '.MuiInputBase-input': {
                        padding: '15px 24px',
                        height: '18px',
                    },
                    '.MuiFormLabel-root.MuiInputLabel-filled.MuiInputLabel-shrink': {
                        transform: 'translate(12px, 2px) scale(0.75)',
                    },
                    '.MuiFilledInput-root.MuiFilledInput-underline:after': {
                        content: 'unset',
                    },
                    '.MuiFilledInput-root.MuiFilledInput-underline:before': {
                        content: 'unset',
                    },
                }),
            }
        }
    }
};
