// React
import React, { useEffect, useState } from 'react';
import { SAAnalytics } from '../../Background/Analytics/SAAnalytics';
import { CoreApp } from '../Shared/Components/CoreApp/CoreApp';
import CssBaseline from '@mui/material/CssBaseline';
import ResponsiveDrawer from '../Shared/Components/ResponsiveDrawer/ResponsiveDrawer';
import CircularProgress from '@mui/material/CircularProgress';
import { OnboardContent } from './Components/OnboardContent';
import { OnboardMenu } from './Components/OnboardMenu';
function App() {
    useEffect(() => {
        SAAnalytics.recordPageOpened('Onboard');
    }, []);

    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setTimeout(() => {
            setLoading(false);
        }, 1);
    }, []);

    return (
        <CoreApp>
            <CssBaseline enableColorScheme />
            { loading ?
                <div className="App">
                    <CircularProgress/>
                </div>
                :
                <ResponsiveDrawer
                    ContentComponent={OnboardContent}
                    MenuComponent={OnboardMenu}
                    forcedTheme="dark"
                />
            }
        </CoreApp>
    )

}

export default App;
