import React from "react";

import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Grid from "@mui/material/Grid";
import Link from "@mui/material/Link";

import { Request, Messages } from '../../../../../shared';
import { useStyles } from '../../styles';

import { useTranslation } from 'react-i18next';

export default function LoginForm(props) {
    const classes = useStyles();
    const { t } = useTranslation();
    
    const openSignUp = () => {
        new Request(Messages.Auth.OpenSignUp, { fromOnboard: true }).sendRequest();
    }

    const openForgotPassword = () => {
        new Request(Messages.Auth.OpenForgotPassword, { fromOnboard: true }).sendRequest();
    }
    
    return(
        <div className={classes.paper}>
            <form onSubmit={props.handleSubmit}>
                <Grid container spacing={2} style={{textAlign:"center"}}>
                    <Grid item xs={12}>    
                        <TextField
                            id="username"
                            autoComplete="username"
                            variant="filled"
                            onChange={props.handleChange}
                            label={t("onboard.login.labels.email")}
                            className={classes.loginInput}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            id="password"
                            autoComplete="current-password"
                            variant="filled"
                            onChange={props.handleChange}
                            label={t("onboard.login.labels.password")}
                            type="password"
                            className={classes.loginInput}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Button type="submit" color="primary" variant="contained">
                            {t("onboard.login.buttons.login")}
                        </Button>
                    </Grid>
                    <Grid item xs={12}>
                        <Link href="#" variant="body2" onClick={openSignUp}>
                            {t("onboard.login.signup")}
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link href="#" variant="body2" onClick={openForgotPassword}>
                            {t("onboard.login.forgot_password")}
                        </Link>
                    </Grid>
                </Grid>
            </form>
        </div>
    )
}
